'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { useTranslation } from './Providers';
import { useOffline } from '@/hooks/useOffline';
import { useSync } from '@/hooks/useSync';
import { cn } from '@/lib/utils';

const navigationItems = [
  { key: 'dashboard', href: '/', icon: 'üìä' },
  { key: 'capture', href: '/audit/capture', icon: 'üì∑' },
  { key: 'report', href: '/audit/report', icon: 'üìÑ' },
  { key: 'portfolio', href: '/portfolio', icon: 'üìÅ' },
  { key: 'chat', href: '/team-chat', icon: 'üí¨' },
  { key: 'settings', href: '/settings', icon: '‚öôÔ∏è' },
  { key: 'calendar', href: '/calendar', icon: 'üìÖ' },
];

export function Navigation() {
  const pathname = usePathname();
  const { t } = useTranslation();
  const isOffline = useOffline();
  const { pendingItems, syncing, lastSync } = useSync();

  return (
    <nav className="w-64 bg-card border-r border-border flex flex-col">
      {/* Header */}
      <div className="p-6 border-b border-border">
        <h1 className="text-xl font-bold text-foreground">AuditPro</h1>
        <div className="flex items-center gap-2 mt-2">
          <div className={cn(
            "w-2 h-2 rounded-full",
            isOffline ? "bg-red-500" : "bg-green-500"
          )} />
          <span className="text-sm text-muted-foreground">
            {isOffline ? t('common.offline') : t('common.online')}
          </span>
        </div>
      </div>

      {/* Sync Status */}
      {(pendingItems > 0 || syncing) && (
        <div className="p-4 bg-muted/50 border-b border-border">
          <div className="text-sm">
            {syncing ? (
              <div className="flex items-center gap-2">
                <div className="animate-spin rounded-full h-3 w-3 border-b border-primary" />
                <span>{t('common.syncing')}</span>
              </div>
            ) : (
              <span>{pendingItems} √©l√©ments en attente</span>
            )}
          </div>
          {lastSync && (
            <div className="text-xs text-muted-foreground mt-1">
              {t('common.lastSync')}: {lastSync.toLocaleTimeString()}
            </div>
          )}
        </div>
      )}

      {/* Navigation Links */}
      <div className="flex-1 py-4">
        <ul className="space-y-1 px-3">
          {navigationItems.map((item) => {
            const isActive = pathname === item.href;
            return (
              <li key={item.key}>
                <Link
                  href={item.href}
                  className={cn(
                    "flex items-center gap-3 px-3 py-2 rounded-md text-sm font-medium transition-colors",
                    isActive
                      ? "bg-primary text-primary-foreground"
                      : "text-muted-foreground hover:text-foreground hover:bg-muted"
                  )}
                >
                  <span className="text-lg">{item.icon}</span>
                  {t(`navigation.${item.key}`)}
                </Link>
              </li>
            );
          })}
        </ul>
      </div>

      {/* Footer */}
      <div className="p-4 border-t border-border">
        <div className="text-xs text-muted-foreground text-center">
          Version 1.0.0
        </div>
      </div>
    </nav>
  );
}
