'use client';

import { useState, useEffect } from 'react';
import { useOffline } from './useOffline';

interface SyncState {
  syncing: boolean;
  lastSync: Date | null;
  pendingItems: number;
  error: string | null;
}

export function useSync() {
  const isOffline = useOffline();
  const [syncState, setSyncState] = useState<SyncState>({
    syncing: false,
    lastSync: null,
    pendingItems: 0,
    error: null,
  });

  // Simulate sync when coming back online
  useEffect(() => {
    if (!isOffline && syncState.pendingItems > 0) {
      performSync();
    }
  }, [isOffline]);

  const performSync = async () => {
    setSyncState(prev => ({ ...prev, syncing: true, error: null }));

    try {
      // Simulate sync process
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      setSyncState(prev => ({
        ...prev,
        syncing: false,
        lastSync: new Date(),
        pendingItems: 0,
      }));
    } catch (error) {
      setSyncState(prev => ({
        ...prev,
        syncing: false,
        error: 'Erreur lors de la synchronisation',
      }));
    }
  };

  const addPendingItem = () => {
    setSyncState(prev => ({
      ...prev,
      pendingItems: prev.pendingItems + 1,
    }));
  };

  const manualSync = () => {
    if (!isOffline) {
      performSync();
    }
  };

  return {
    ...syncState,
    isOffline,
    addPendingItem,
    manualSync,
  };
}
